import BaseModel from './baseModel';

export default class Mtah5 extends BaseModel {
    portrait(query) {
        // 用户画像
        return super.get(`/ctr_user_portrait?${query}`)
    }
    para(query) {
        // 终端信息
        return super.get(`/ctr_client/get_by_para?${query}`)
    }
}


