import axios from 'axios';


class CacheStorage {
  static _storage = {}
  static _key(path, {params={}}={}) {
    return `${path}?${JSON.stringify(params)}`;
  }  
  static get(path, config) {
    let key = CacheStorage._key(path, config);
    return CacheStorage._storage[key];
  }
  static set(path, config, data) {
    let key = CacheStorage._key(path, config);
    CacheStorage._storage[key] = data;
  }
  static save() {
    // localStorage.setItem('_DS_cache_' + this._key, JSON.stringify(this._data || data));
  }
  static read() {
    // this._data = JSON.parse(localStorage.getItem('_DS_cache_' + this._key));
  }
}



export default class BaseModel {
  _basePath = '';

  static axios = axios;

  constructor(basePath) {
    this._basePath = basePath || '';
  }

  create(config) {
    return axios.create(config);
  }

  get(path, config) {
    // 优先从请求网络, 网络失败再从缓存获取
    return axios.get(this._basePath + path, config)
    .then(({data}) => {
      CacheStorage.set(path, config, data);
      return data;
    })
    .catch(e => {
      console.log({e});
      let cache = CacheStorage.get(path, config);
      //  && e.code == 'ECONNABORTED'
      if(cache){
        return cache;
      }else{
        throw e.response && e.response.data;
      }
    });
      
  }

  getFromCache(path, config) {
    // 优先从缓存获取，但仍然请求网络
    let cache = CacheStorage.get(path, config);
    // if(cache){console.log('data from cache');}
    let newData = axios.get(this._basePath + path, config)
      .then(({
        data
      }) => {
        CacheStorage.set(path, config, data);
        return data;
      })
      .catch(e => {
        throw e.response && e.response.data;
      });
    return cache ? Promise.resolve(cache) : newData;
  }

  OLD_get(path, config) {
    return axios.get(this._basePath + path, config)
      .then(res => res.data)
      .catch(e => {
        throw e.response && e.response.data;
      });
  }

  post(path, data, config) {
    return axios.post(this._basePath + path, data, config)
      .then(res => res.data)
      .catch(e => {
        throw e.response && e.response.data;
      });
  }

  put(path, data, config) {
    return axios.put(this._basePath + path, data, config)
      .then(res => res.data)
      .catch(e => {
        throw e.response && e.response.data;
      });
  }

  _keyBy(arr, filter) {
    let re = {};
    (arr || []).forEach(v => {
      re[v[filter]] = v;
    });
    Object.defineProperty(re, "_list_", {
      value: arr,
      enumerable: false
    });
    return re;
  }

  _parseJSON(obj, key) {
    try {
      obj[key] = JSON.parse(obj[key]);
    } catch (error) {
      console.warn('Model JSON.parse Error', obj[key], error);
      obj[key] = {
        _raw: obj[key]
      };
    }
    return obj;
  }

}



