import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import ArticleList from '@/components/ArticleList'

Vue.use(Router)

export default new Router({
  //mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/article-list',
      name: 'ArticleList',
      component: ArticleList
    }
  ]
})



