<template>
  <div class="user-portrait">
    <header>
      <img class="header" src="../assets/userPortrait/title.png">
      <img class="s-header-bling" src="../assets/articleList/title-bling.png">
    </header>
    <div class="user-portrait-main">
      <!-- 用户学历分布 -->
      <div class="s-grade">
        <img src="../assets/userPortrait/light.png">
        <section ref="grade"></section>
      </div>
      <!-- 累计启动次数 -->
      <div class="s-userCompare">
        <img src="../assets/userPortrait/light.png">
        <section>
          <p>
            <span>累计启动次数</span>
            <label>{{SessionCount}}</label>
          </p>
          <p>
            <span>累计用户</span>
            <label>{{userRealtime.TotalUser}}</label>
          </p>
          <p>
            <span>活跃用户</span>
            <label>{{userRealtime.ActiveUser}}</label>
          </p>
          <p>
            <span>新增用户</span>
            <label>{{userRealtime.NewUser}}</label>
          </p>
        </section>
      </div>
      <!-- 职业分布Top5 -->
      <div class="s-profession">
        <img src="../assets/userPortrait/light.png">
        <section id="profession" ref="profession"></section>
      </div>
      <!-- 访问占比 -->
      <div class="s-access">
        <img src="../assets/userPortrait/light.png">
        <section id="access" ref="access"></section>
      </div>
      <!-- 年龄占比 -->
      <div class="s-age">
        <img src="../assets/userPortrait/light.png">
        <section ref="age"></section>
      </div>
      <!-- 设备分析 -->
      <div class="s-equipment">
        <img src="../assets/userPortrait/light.png">
        <section ref="equipment"></section>
      </div>
    </div>
  </div>
</template>

<script>
import md5 from "blueimp-md5";
import createHmac from "create-hmac";
import echarts from "echarts";
import highcharts from "highcharts";
import highcharts3d from "highcharts/highcharts-3d";
import cylinder from "highcharts/modules/cylinder";
highcharts3d(highcharts);
cylinder(highcharts);
export default {
  name: "",
  data() {
    return {
      h5_app_id: 500618046,
      h5_secret_key: "39e768650e51c6b7a69919ad91df151b",
      android_app_id: 3103486874,
      android_app_key: "ACT58LTH5E7U",
      ios_app_id: 3203486874,
      ios_app_key: "I22WXZGI2G2C",
      intervalId: 0,
      SessionCount: 0,
      age: {},
      client: {},
      grade: {},
      profession: {},
      terminal: {},
      userRealtime: {},
      start_date: "2018-06-08",
      size: 1,
      end_date: ""
    };
  },
  methods: {
    parseQuerySign(query) {
      // 解析签名sign，返回参数串
      let queryStrs = Object.entries(query)
        .sort()
        .map(a => a[0] + "=" + a[1]);
      let sign = md5(this.h5_secret_key + queryStrs.join(""));
      queryStrs.push(`sign=${sign}`);
      return queryStrs.join("&");
    },
    parseAppQuerySign(params, query, app_key) {
      let queryStrs = Object.entries(query)
        .sort()
        .map(a => a[0] + "=" + a[1]);
      let paramsURI = encodeURIComponent(params); // params进行url编码
      let queryConnect = queryStrs.join("&"); // query用&连接
      let queryURI = encodeURIComponent(queryConnect); // query进行url编码
      let origin = "GET&" + paramsURI + "&" + queryURI; // 构造源串
      let token = app_key + "&"; // 构造秘钥
      let hmac = createHmac("sha1", Buffer.from(token))
        .update(origin)
        .digest("hex"); // hmac_sha1加密，并以原始二进制输出
      let sign = md5(hmac); // md5编码，得到sign密钥
      queryStrs.push(`sign=${sign}`);
      return queryStrs.join("&");
    },
    getScreenWidth() {
      let width = window.screen.width; // 计算屏幕宽度，自适应字体
      if (width < 2000) {
        this.size = 1;
      } else if (width < 4000) {
        this.size = 2;
      } else if (width < 6000) {
        this.size = 3;
      } else if (width < 8000) {
        this.size = 4;
      } else {
        this.size = 5;
      }
    },
    userPortrait() {
      // 用户画像：年龄、性别、职业、学历
      let query = {
        app_id: this.h5_app_id,
        start_date: this.start_date,
        end_date: this.end_date,
        idx: "sex,age,grade,profession"
      };
      this.$Model.H5.portrait(this.parseQuerySign(query)).then(data => {
        let portrait = data.data;
        let { age, grade, profession } = portrait;
        this.age = age;
        this.grade = grade;
        this.profession = profession;
        this.draw_userGrade();
        this.draw_userAge();
        this.draw_userProfession();
      });
    },
    userCompare() {
      let paramsOffline = "/ctr_user_basic/get_offline_data";
      let paramsRealtime = "/ctr_user_basic/get_realtime_data";
      let queryOffline = {
        app_id: this.android_app_id,
        start_date: this.start_date,
        end_date: this.end_date,
        idx: "10103",
        version: "",
        channel: ""
      };
      let queryOfflineIos = {
        app_id: this.ios_app_id,
        start_date: this.start_date,
        end_date: this.end_date,
        idx: "10103",
        version: "",
        channel: ""
      };
      let queryRealtime = {
        app_id: this.android_app_id,
        idx: "10101,10102,10104",
        version: "",
        channel: ""
      };
      let queryRealtimeIos = {
        app_id: this.ios_app_id,
        idx: "10101,10102,10104",
        version: "",
        channel: ""
      };
      let OfflineAndroid = this.$Model.App.offline(
        this.parseAppQuerySign(
          paramsOffline,
          queryOffline,
          this.android_app_key
        )
      );
      let RealtimeAndroid = this.$Model.App.real(
        this.parseAppQuerySign(
          paramsRealtime,
          queryRealtime,
          this.android_app_key
        )
      );
      let OfflineIos = this.$Model.App.offline(
        this.parseAppQuerySign(paramsOffline, queryOfflineIos, this.ios_app_key)
      );
      let RealtimeIos = this.$Model.App.real(
        this.parseAppQuerySign(
          paramsRealtime,
          queryRealtimeIos,
          this.ios_app_key
        )
      );
      Promise.all([
        OfflineAndroid,
        RealtimeAndroid,
        OfflineIos,
        RealtimeIos
      ]).then(([OffA, RealA, OffIos, RealIos]) => {
        let SessionCount = 0;
        Object.keys(OffA.ret_data).forEach(b => {
          SessionCount = SessionCount + parseInt(OffA.ret_data[b].SessionCount);
        });
        Object.keys(OffIos.ret_data).forEach(b => {
          SessionCount =
            SessionCount + parseInt(OffIos.ret_data[b].SessionCount);
        });
        this.SessionCount = SessionCount.toLocaleString();
        this.userRealtime = RealA.ret_data;
        let { TotalUser, NewUser, ActiveUser } = this.userRealtime;
        this.userRealtime.TotalUser =
          parseInt(TotalUser) + parseInt(RealIos.ret_data.TotalUser);
        this.userRealtime.NewUser =
          parseInt(NewUser) + parseInt(RealIos.ret_data.NewUser);
        this.userRealtime.ActiveUser =
          parseInt(ActiveUser) + parseInt(RealIos.ret_data.ActiveUser);
      });
    },
    userClient() {
      // 终端信息Top4
      let query = {
        app_id: this.h5_app_id,
        start_date: this.start_date,
        end_date: this.end_date,
        type_id: "2",
        idx: "pv"
      };
      this.$Model.H5.para(this.parseQuerySign(query)).then(data => {
        this.client = data.data;
        this.draw_access();
      });
    },
    userTerminal() {
      this.terminal = {
        其它: 0,
        OPPO: 0,
        VIVO: 0,
        华为: 0,
        小米: 0,
        苹果: 0
      };
      let params = "/ctr_terminal_anal/get_offline_data";
      let query = {
        app_id: this.android_app_id,
        start_date: this.start_date,
        end_date: this.end_date,
        idx: "10301,10302,10303",
        ty: 5,
        version: ""
      };
      let queryIos = {
        app_id: this.ios_app_id,
        start_date: this.start_date,
        end_date: this.end_date,
        idx: "10301,10302,10303",
        ty: 5,
        version: ""
      };
      let android = this.$Model.App.offlineTerminal(
        this.parseAppQuerySign(params, query, this.android_app_key)
      );
      let ios = this.$Model.App.offlineTerminal(
        this.parseAppQuerySign(params, queryIos, this.ios_app_key)
      );
      Promise.all([android, ios]).then(([androidData, iosData]) => {
        let terminalAndroid = androidData.ret_data;
        let terminalIos = iosData.ret_data;
        terminalIos
          .filter(a => a.Terminal.indexOf("iPhone") != -1)
          .forEach(item => {
            this.terminal.苹果 =
              this.terminal.苹果 + parseInt(item.SessionCount);
          });
        terminalAndroid
          .filter(a => a.Terminal.indexOf("OPPO") != -1)
          .forEach(item => {
            this.terminal.OPPO =
              this.terminal.OPPO + parseInt(item.SessionCount);
          });
        terminalAndroid
          .filter(a => a.Terminal.indexOf("vivo") != -1)
          .forEach(item => {
            this.terminal.VIVO =
              this.terminal.VIVO + parseInt(item.SessionCount);
          });
        terminalAndroid
          .filter(a => a.Terminal.indexOf("HUAWEI") != -1)
          .forEach(item => {
            this.terminal.华为 =
              this.terminal.华为 + parseInt(item.SessionCount);
          });
        terminalAndroid
          .filter(a => a.Terminal.indexOf("Xiaomi") != -1)
          .forEach(item => {
            this.terminal.小米 =
              this.terminal.小米 + parseInt(item.SessionCount);
          });
        terminalAndroid
          .filter(
            a =>
              a.Terminal.indexOf("OPPO") == -1 &&
              a.Terminal.indexOf("vivo") == -1 &&
              a.Terminal.indexOf("HUAWEI") == -1 &&
              a.Terminal.indexOf("Xiaomi") == -1
          )
          .forEach(item => {
            this.terminal.其它 =
              this.terminal.其它 + parseInt(item.SessionCount);
          });
        this.draw_equipment();
      });
    },
    allFunction() {
      this.userPortrait();
      this.userCompare();
      this.userClient();
      this.userTerminal();
    },
    draw_userGrade() {
      // 用户学历分布
      let dom = this.$refs.grade;
      let mycharts = echarts.init(dom);
      let series_data = [];
      let indicator_data = [];
      Object.keys(this.grade).forEach(key => {
        series_data.push(this.grade[key]);
        indicator_data.push({
          name: key,
          max: parseInt(this.grade[key]) * 2 + 500
        });
      });
      for (let i = 0; i < 5 - series_data.length; i++) {
        indicator_data.push({ name: "未知" });
      }
      let option = {
        title: {
          text: "用户学历分布",
          x: "center",
          padding: [20, 0, 0, 0],
          textStyle: {
            fontSize: 25 * this.size,
            color: "#6efff0"
          }
        },
        tooltip: {},
        radar: {
          name: {
            textStyle: {
              color: "#fff",
              fontSize: 15 * this.size
            }
          },
          radius: "55%",
          splitLine: {
            lineStyle: {
              color: "#6efff0"
            }
          },
          splitArea: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: "#6efff0"
            }
          },
          indicator: indicator_data
        },
        series: [
          {
            name: "",
            type: "radar",
            backgroundColor: "none",
            center: ["50%", "50%"],
            itemStyle: {
              normal: {
                color: "#6efff0",
                opacity: 0
              }
            },
            lineStyle: {
              color: "6efff0"
            },
            areaStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(1, 0, 0, 0, [
                  {
                    offset: 0,
                    color: "#6efff0"
                  },
                  {
                    offset: 0.5,
                    color: "#169cae"
                  },
                  {
                    offset: 1,
                    color: "#6efff0"
                  }
                ])
              }
            },
            data: [
              {
                value: series_data,
                name: ""
              }
            ]
          }
        ]
      };
      mycharts.setOption(option);
      window.onresize = mycharts.resize;
    },
    draw_userAge() {
      // 用户年龄占比
      let dom = this.$refs.age;
      let mycharts = echarts.init(dom);
      let legend_data = [];
      let series_data = [];
      let colors = [
        "#f4457a",
        "#b267e7",
        "#16d3e0",
        "#01d356",
        "#ffc800",
        "#16d3e0",
        "#e0a516"
      ];
      Object.keys(this.age).forEach(key => {
        legend_data.push(key);
        series_data.push({
          value: parseInt(this.age[key]),
          name: key,
          itemStyle: {
            color: ""
          }
        });
      });
      for (let i of series_data.keys()) {
        series_data[i].itemStyle.color = colors[i];
      }
      let option = {
        title: {
          text: "年龄占比",
          x: "center",
          padding: [20, 0, 0, 0],
          textStyle: {
            fontSize: 25 * this.size,
            color: "#6efff0"
          }
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          x: "left",
          y: "center",
          left: "5%",
          itemWidth: 5 * this.size,
          itemHeight: 13 * this.size,
          itemGap: 10 * this.size,
          textStyle: {
            color: "#fff",
            padding: [0, 0, 0, 10],
            fontSize: 15 * this.size
          },
          padding: [0, 0, 0, 30],
          data: legend_data
        },
        calculable: true,
        series: [
          {
            name: "年龄占比",
            type: "pie",
            radius: ["40%", "60%"],
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: "center"
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "30",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: series_data
          }
        ]
      };
      mycharts.setOption(option);
    },
    draw_userProfession() {
      // 用户职业分布Top5
      let xAxis_data = [];
      let series_data = [];
      let sum = 0;
      let dataShadow = [100, 100, 100, 100, 100]; // 阴影部分max
      let keys = Object.keys(this.profession).sort(
        (a, b) => this.profession[b] - this.profession[a]
      );
      for (let i in keys) {
        sum = sum + parseInt(this.profession[keys[i]]);
        xAxis_data.push(keys[i]);
        series_data.push({
          name: keys[i],
          y: parseInt(this.profession[keys[i]]),
          color: "#ffcd00"
        });
      }
      for (let i of series_data.keys()) {
        series_data[i].y = parseFloat(
          ((series_data[i].y * 100) / sum).toFixed(1)
        ); // 化为百分比
      }
      xAxis_data = xAxis_data.slice(0, 5);
      series_data = series_data.slice(0, 5);
      let chart = highcharts.chart("profession", {
        chart: {
          type: "cylinder",
          backgroundColor: "none",
          options3d: {
            enabled: true,
            alpha:10,
            beta:10,
            depth: 45 * this.size,
            viewDistance: 28 * this.size
          }
        },
        title: {
          text: "职业分布Top5",
          style: {
            color: "#6efff0",
            fontSize: 25 * this.size + "px",
            fontWeight: "bold"
          }
        },
        credits: { enabled: false },
        plotOptions: {
          series: {
            depth: 38 * this.size,
            colorByPoint: true,
            dataLabels: {
              align: "center",
              enabled: true,
              formatter: function() {
                return this.y + "%";
              },
              color: "#fff",
              style: { fontSize: 15 * this.size + "px" }
            }
          }
        },
        xAxis: {
          categories: xAxis_data,
          labels: {
            y: 20 * this.size,
            style: { fontSize: 15 * this.size + "px", color: "#fff" }
          },
          gridLineWidth: 0
        },
        yAxis: {
          visible: false
        },
        series: [
          {
            data: series_data,
            name: "职业Top5",
            showInLegend: false
          }
        ]
      });
    },
    draw_equipment() {
      // 访问设备分析
      let dom = this.$refs.equipment;
      let mycharts = echarts.init(dom);
      let xAxis_data = [];
      let series_data = [];
      let dataShadow = [2000, 2000, 2000, 2000, 2000, 2000]; // 阴影部分max
      for (let [key, value] of Object.entries(this.terminal)) {
        xAxis_data.push(key);
        series_data.push(value);
      }
      let option = {
        color: ["#ffcd00"],
        title: {
          text: "设备分析",
          x: "center",
          padding: [20, 0, 0, 0],
          textStyle: {
            fontSize: 25 * this.size,
            color: "#6efff0"
          }
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        grid: {
          left: "4%",
          right: "4%",
          bottom: "3%",
          width: "80%",
          containLabel: true
        },
        yAxis: [
          {
            type: "category",
            data: xAxis_data,
            axisLine: {
              lineStyle: {
                width: 0
              }
            },
            axisTick: {
              show: false
            },
            axisLabel: {
              show: true,
              textStyle: {
                color: "#fff",
                fontSize: 15 * this.size
              }
            }
          }
        ],
        xAxis: [
          {
            show: false,
            type: "value"
          }
        ],
        series: [
          {
            // 阴影部分
            name: "Max",
            type: "bar",
            label: {
              show: false
            },
            itemStyle: {
              normal: {
                color: "#1e244a",
                barBorderRadius: [
                  10 * this.size,
                  10 * this.size,
                  10 * this.size,
                  10 * this.size
                ]
              }
            },
            barWidth: "25%",
            barGap: "-100%",
            barCategoryGap: "30%",
            data: dataShadow,
            animation: false
          },
          {
            name: "占比",
            type: "bar",
            barWidth: "25%",
            itemStyle: {
              normal: {
                barBorderRadius: [
                  10 * this.size,
                  10 * this.size,
                  10 * this.size,
                  10 * this.size
                ],
                label: {
                  show: true,
                  position: "right",
                  formatter: "{c}",
                  textStyle: {
                    color: "#fff",
                    fontSize: 15 * this.size
                  }
                }
              }
            },
            data: series_data
          }
        ]
      };
      mycharts.setOption(option);
    },
    draw_access() {
      // 访问占比
      let series_data = [];
      let colors = ["#fa5980", "#027eef", "#a362f9", "#54e6ce", "#f88968"];
      let keys = Object.keys(this.client).sort(
        (a, b) => this.client[b] - this.client[a]
      );
      for (let i in keys) {
        series_data.push({
          y: this.client[keys[i]].pv,
          name: keys[i],
          color: colors[i]
        });
      }
      series_data = series_data.slice(0, 4);
      var chart = highcharts.chart("access", {
        chart: {
          type: "pie",
          backgroundColor: "none",
          options3d: {
            enabled: true,
            alpha: 45
          }
        },
        title: {
          text: "访问占比",
          y: 30 * this.size,
          style: {
            color: "#6efff0",
            fontSize: 25 * this.size + "px",
            fontWeight: "bold"
          }
        },
        credits: {
          enabled: false
        },
        legend: {
          align: "center",
          itemStyle: { color: "#fff", fontSize: 15 * this.size + "px" },
          itemDistance: 30 * this.size,
          symbolPadding: 8 * this.size
        },
        plotOptions: {
          pie: {
            innerSize: 100,
            depth: 45 * this.size,
            dataLabels: false,
            showInLegend: true
          }
        },
        series: [
          {
            name: "访问占比",
            innerSize: "50%",
            colorByPoint: true,
            data: series_data
          }
        ]
      });
    }
  },
  created() {
    let now = new Date();
    let year = now.getFullYear();
    let month = (now.getMonth() + 1 + "").padStart(2, "0");
    let day = (now.getDate() + "").padStart(2, "0");
    this.end_date = year + "-" + month + "-" + day;
    this.getScreenWidth();
    this.intervalId = setInterval(this.allFunction, 100000);
  },
  mounted() {
    this.allFunction();
  },
  beforeDestroy() {
    clearInterval(this.intervalId);
  }
};
</script>

<style lang="scss" scoped>
.user-portrait {
  position: relative;
  width: 100vw;
  height: 100vh;
  color: #fff;
  background: url("../assets/userPortrait/bg.png") no-repeat;
  background-size: 100% 100%;
  background-position: center center;
  overflow: hidden;

  header {
    width: 100%;
    height: 9vh;
    position: relative;
    padding: 20px;

    .header {
      height: 100%;
    }
    .s-header-bling {
      position: absolute;
      top: 58%;
      left: 50%;
      transform: translate(-50%, -50%);
      height: 100%;

      animation: scale 3.5s infinite;
      @keyframes scale {
        0% {
          opacity: 0.2;
        }
        100% {
          opacity: 1;
        }
      }
    }
  }
  &-main {
    position: relative;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 0 3%;
    div {
      position: relative;
      width: 29%;
      height: 41vh;
      margin: 1vh 0;
      background: url("../assets/userPortrait/child-center.png");
      background-repeat: no-repeat;
      background-size: 100% 100%;
      background-position: center;
      section {
        height: 100%;
      }
      img {
        position: absolute;
        width: 30%;
        height: 30%;
        animation: move 3s ease infinite;
      }
      @keyframes move {
        0% {
          left: 0;
          top: -14.1%;
          opacity: 0;
          transform: rotate(0deg);
        }
        50% {
          opacity: 1;
          transform: rotate(0deg);
        }
        100% {
          left: 70%;
          top: -14.1%;
          opacity: 0;
          transform: rotate(0deg);
        }
      }
    }
    .s-userCompare,
    .s-age {
      width: 36%;
    }
    .s-userCompare {
      section {
        height: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        p {
          width: 33%;

          span,
          label {
            display: block;
          }
          span {
            color: #6efff0;
            font-size: 1.3vw;
            padding: 20px 0;
          }
          label {
            font-weight: bold;
            font-size: 1.5vw;
          }
        }
        p:first-child {
          width: 100%;
          span {
            font-size: 2.2vw;
            background-image: linear-gradient(
              to bottom,
              #6efff0,
              #6efff0,
              #487ce9
            );
            -webkit-background-clip: text;
            color: transparent;
          }
          label {
            font-size: 3vw;
          }
        }
      }
    }
  }
}
</style>


