<template>
  <div class="user-article-list">
    <header>
      <img class="header" src="../assets/articleList/title.png">
      <img class="s-header-bling" src="../assets/articleList/title-bling.png">
    </header>
    <div class="user-article-list-main">
      <img class="s-animate" src="../assets/userPortrait/light.png">
      <table>
        <thead>
          <th class="s-rank">排名</th>
          <th class="s-bg">封面</th>
          <th class="s-title">标题</th>
          <th>编辑</th>
          <th>审核</th>
          <th>阅读数</th>
          <th>发布时间</th>
        </thead>
        <tbody>
          <tr class="animated bounceInLeft" v-for="(item, index) in list" :key="item.id">
            <td class="s-index">
              <span>{{(index + 1) + count}}</span>
            </td>
            <td>
              <img :src="item.thumbnail_urls[0] || defaultBg">
            </td>
            <td class="s-td-title">{{item.title}}</td>
            <td>{{item.author_nickname || item.author_name}}</td>
            <td>{{item.publisher_nickname || item.publisher_name}}</td>
            <td>{{item.view_count}}</td>
            <td>{{item.publish_time | change_time}}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import "animate.css";
export default {
  data() {
    return {
      list: [],
      intervalId: 0,
      count: 0,
      defaultBg: require("../assets/articleList/td-bg.jpg")
    };
  },
  created() {
    this.intervalId = setInterval(this.articleList, 100000);
  },
  mounted() {
    this.articleList();
  },

  methods: {
    articleList() {
      // 文章阅读排行Top6
      this.$Model.User.contents(1528387200, 300).then(data => {
        let list = data.filter(item => item.type == "default").slice(0, 30);
        // this.list = list.slice(0, 6);
        let num = Math.floor(list.length / 6);
        for (let i = 0; i < Math.ceil(list.length / 6); i++) {
          setTimeout(() => {
            this.count = i * 6;
            this.list = list.slice(i * 6, (i + 1) * 6);
          }, 20000 * i);
        }
      });
    }
  },
  filters: {
    change_time(time) {
      let stopTime = new Date(time * 1000);
      let year = stopTime.getFullYear();
      let month = (stopTime.getMonth() + 1 + "").padStart(2, "0");
      let day = (stopTime.getDate() + "").padStart(2, "0");
      let hour = (stopTime.getHours() + "").padStart(2, "0");
      let min = (stopTime.getMinutes() + "").padStart(2, "0");
      return year + "." + month + "." + day + "-" + hour + ":" + min;
    }
  },
  beforeDestroy() {
    clearInterval(this.intervalId);
  }
};
</script>

<style lang="scss">
.user-article-list {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;

  height: 100%;
  //margin: 30px;
  font-size: 1.3vw;
  overflow: hidden;
  background: url("../assets/articleList/bg.png") no-repeat;
  background-size: 100% 100%;
  background-position: center center;

  header {
    width: 100%;
    height: 10vh;
    position: relative;

    img {
      height: 100%;
    }
    .s-header-bling {
      position: absolute;
      top: 60%;
      left: 50.5%;
      transform: translate(-50%, -50%);
      height: 100%;

      animation: scale 3s infinite;
      @keyframes scale {
        0% {
          opacity: 0.2;
        }
        100% {
          opacity: 1;
        }
      }
    }
  }
  &-main {
    position: relative;
    width: 100%;
    overflow: hidden;
    align-self: flex-start;
    padding: 1.5vw;
    margin: 0 3vw;

    background: url("../assets/articleList/main-bg.png") no-repeat;
    background-size: 100% 100%;
    background-position: center center;

    .s-animate {
      position: absolute;
      width: 30%;
      height: 30%;
      animation: move 3.5s ease infinite;
    }
    @keyframes move {
      0% {
        left: 20%;
        top: -12.5%;
        opacity: 0;
        transform: rotate(0deg);
      }
      50% {
        opacity: 1;
        transform: rotate(0deg);
      }
      100% {
        left: 50%;
        top: -12.5%;
        opacity: 0;
        transform: rotate(0deg);
      }
    }

    table {
      width: inherit;
      border: none;
      // border-collapse: collapse;
      table-layout: fixed;

      background: url("../assets/articleList/table-bg.png") no-repeat;
      background-size: 100% 100%;
      background-position: center center;

      th:first-child,
      td:first-child {
        border-left: none;
      }
      tr:last-child {
        td {
          border-bottom: none;
        }
      }
      thead {
        background: url("../assets/articleList/thead-bg.png") no-repeat;
        background-size: 100% 100%;
        background-position: center center;

        th {
          height: 6vh;
          font-weight: normal;

          border-left: 1px solid #00c4d4;
          border-bottom: 1px solid #1568ba;
        }
        .s-rank {
          width: 6%;
        }
        .s-bg {
          width: 11%;
        }
        .s-title {
          width: 30%;
        }
      }
      tr:hover {
        background-color: #004d84;
      }
      tr {
        animation-duration: 3s;
        td {
          height: 11.5vh;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;

          border-left: 1px solid #00c4d4;
          border-bottom: 1px solid #1568ba;

          img {
            width: 80%;
            height: 80%;
            line-height: 80%;
            border-radius: 5px;
          }
        }
        .s-index {
          span {
            display: inline-block;
            width: 4vw;
            height: 3.8vw;
            line-height: 3.8vw;
            font-size: 2vw;
            font-weight: bold;

            border: 2px solid #1568ba;
            border-radius: 5px;
            background: linear-gradient(to bottom, #00c5dc, #00c0d9, #004d84);
            background-size: 80% 80%;
            background-repeat: no-repeat;
            background-position: center center;
          }
        }
        .s-td-title {
          text-align: left;
          padding: 0 1vw;
        }
      }
      tr:nth-child(2) {
        animation-delay: 2s;
      }
      tr:nth-child(3) {
        animation-delay: 4s;
      }
      tr:nth-child(4) {
        animation-delay: 6s;
      }
      tr:nth-child(5) {
        animation-delay: 8s;
      }
      tr:nth-child(6) {
        animation-delay: 10s;
      }
    }
  }
}
</style>



