import BaseModel from './baseModel';

export default class MtaApp extends BaseModel {
    real(query) {
        // 实时信息
        return super.get(`/ctr_user_basic/get_realtime_data?${query}`)
    }

    offline(query) {
        // 离线信息
        return super.get(`/ctr_user_basic/get_offline_data?${query}`)
    }
    
    offlineTerminal(query) {
        // 终端离线信息
        return super.get(`/ctr_terminal_anal/get_offline_data?${query}`)
    }
}


